$htmlSize: 14;

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-5: rgba(31, 32, 65, 0.05);
$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$green: #6FCF97;

$blue: #8BA4F9;
$blue-50: #8BA4F950;

$purple-blue-gradient: linear-gradient(180deg, $purple 0%, $blue 100%);
$purple-blue-gradient-50: linear-gradient(180deg, $purple-50 0%, $blue-50 100%);


@function toRem($value) {
	$remValue: calc($value / $htmlSize) + rem; 
	@return $remValue;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

html {
  font-family: 'Montserrat', Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: toRem(24);
  background: #FFFFFF;
  color: $dark-shade-75;
}

h1, h2, h3 {
  font-weight: 700;
  color: $dark-shade-100;
}

h1 {
  font-size: toRem(24);
  line-height: toRem(29.26);
}

h2 {
  font-size: toRem(19);
  line-height: toRem(23.16);
}

h3 {
  font-size: toRem(12);
  text-transform: uppercase;
  line-height: toRem(14.63);
}

button {
  border: none;
  background-color: initial;
}