$htmlSize: 14;

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-5: rgba(31, 32, 65, 0.05);
$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$green: #6FCF97;

$blue: #8BA4F9;
$blue-50: #8BA4F950;

$purple-blue-gradient: linear-gradient(180deg, $purple 0%, $blue 100%);
$purple-blue-gradient-50: linear-gradient(180deg, $purple-50 0%, $blue-50 100%);


@function toRem($value) {
	$remValue: calc($value / $htmlSize) + rem; 
	@return $remValue;
}
$className: 'input-field';

.#{$className} {
	max-width: toRem(320);
	min-width: toRem(150);
	width: 100%;

	&__header {
		margin-bottom: toRem(5);
	}

	&__main-field {
		position: relative;
		display: flex;

    &:hover {
      .#{$className}__input {
        border-color: $dark-shade-50;
      }
    }
	}

	&__input {
		border: 1px solid $dark-shade-25;
		box-sizing: border-box;
		border-radius: toRem(4);
		height: toRem(42);
		width: 100%;
		padding-left: toRem(15);
    padding-right: toRem(91);
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;

		&:hover, &:focus {
			border-color: $dark-shade-50;
			outline: none;

			&::placeholder {
				color: $dark-shade-75;
			}
		}

		&::placeholder {
			font-family: Montserrat, Arial, sans-serif;
			font-weight: 400;
			color: $dark-shade-25;
			font-size: toRem(14);
			line-height: toRem(18);
		 }
	}

	&__expand-button {
		position: absolute;
		right: toRem(10);
		align-self: center;
		color: $dark-shade-50;
		width: toRem(24);
		height: toRem(24);
	}

	&_hovered, &_expandable {
		.#{$className}__input::placeholder {
			color: $dark-shade-75;
		}
	}

	&_hovered {
		.#{$className}__input {
			border-color: $dark-shade-50;
		}
	}

	&_opened {
		.#{$className}__input {
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
			border-color: $dark-shade-50;
		}
	}
}

