$htmlSize: 14;

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-5: rgba(31, 32, 65, 0.05);
$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$green: #6FCF97;

$blue: #8BA4F9;
$blue-50: #8BA4F950;

$purple-blue-gradient: linear-gradient(180deg, $purple 0%, $blue 100%);
$purple-blue-gradient-50: linear-gradient(180deg, $purple-50 0%, $blue-50 100%);


@function toRem($value) {
	$remValue: calc($value / $htmlSize) + rem; 
	@return $remValue;
}
$className: 'colors-and-type';

.#{$className} {
	padding: 0 toRem(20) 0;
	margin: toRem(67) 0 0 toRem(20);
	&__main {
		display: flex;
	}

	&__color-palette {
		margin-top: toRem(1);
		list-style-type: none;
	}

	&__color-palette-item {
		margin-bottom: toRem(20);

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__text-examples {
		list-style-type: none;
		margin-left: auto;
		margin-right: toRem(28);
	}

	&__text-examples-item {
		display: flex;
		margin-bottom: toRem(51);

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__text-examples-title {
		color: $dark_shade_25;
		text-align: right;
		font-weight: 400;
		min-width: toRem(50);	

		&_header {
			text-transform: uppercase;
		}
	}

	&__text-examples-description {
		margin-left: toRem(30);
		white-space: pre;
	}

}