$htmlSize: 14;

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-5: rgba(31, 32, 65, 0.05);
$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$green: #6FCF97;

$blue: #8BA4F9;
$blue-50: #8BA4F950;

$purple-blue-gradient: linear-gradient(180deg, $purple 0%, $blue 100%);
$purple-blue-gradient-50: linear-gradient(180deg, $purple-50 0%, $blue-50 100%);


@function toRem($value) {
	$remValue: calc($value / $htmlSize) + rem; 
	@return $remValue;
}
$className: 'dropdown-counter';

.#{$className} {
	position: relative;
	max-width: toRem(320);
	min-width: toRem(150);
	width: 100%;

	&__menu {
		position: absolute;
		width: 100%;
		border: 1px solid $dark-shade-50;
		border-top-width: 0;
		box-sizing: border-box;
		border-radius: 0 0 toRem(4) toRem(4);
		box-shadow: 0 toRem(10) toRem(20) 0 $dark-shade-5;
		display: none;
		z-index: 999;
		background-color: white;
    padding-left: toRem(15);
    padding-right: toRem(7);
    padding-bottom: toRem(7);
	}

  &__items {
    list-style-type: none;
  }

  &__item {
    margin-top: toRem(6);
  }

  &__control-panel-wrapper {
    margin-top: toRem(10);
  }

	&_opened {
		.#{$className}__menu {
			display: block;
		}
	}
}