$htmlSize: 14;

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-5: rgba(31, 32, 65, 0.05);
$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$green: #6FCF97;

$blue: #8BA4F9;
$blue-50: #8BA4F950;

$purple-blue-gradient: linear-gradient(180deg, $purple 0%, $blue 100%);
$purple-blue-gradient-50: linear-gradient(180deg, $purple-50 0%, $blue-50 100%);


@function toRem($value) {
	$remValue: calc($value / $htmlSize) + rem; 
	@return $remValue;
}
$className: 'counter-item';

.#{$className} {
  display: flex;
  align-items: center;

  &__counter {
    display: flex;
    align-items: center;
    margin-left: auto;
    max-width: toRem(92);
    width: 100%;
    justify-content: space-between;
  }

  &__value {
    font-size: toRem(12);
    font-weight: 700;
    line-height: toRem(14.63);
    color: $dark-shade-100;
  }

  &__decrement, &__increment {
    width: toRem(30);
    height: toRem(30);
    border: 1px solid $dark-shade-25;
    border-radius: 50%;
    color: $dark-shade-50;
    font-size: toRem(18);
  }

  &__decrement:disabled, &__increment:disabled {
    opacity: 0.5;
  }
}