$htmlSize: 14;

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-5: rgba(31, 32, 65, 0.05);
$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$green: #6FCF97;

$blue: #8BA4F9;
$blue-50: #8BA4F950;

$purple-blue-gradient: linear-gradient(180deg, $purple 0%, $blue 100%);
$purple-blue-gradient-50: linear-gradient(180deg, $purple-50 0%, $blue-50 100%);


@function toRem($value) {
	$remValue: calc($value / $htmlSize) + rem; 
	@return $remValue;
}
$button: 'button';

.#{$button} {
  font-family: Montserrat, Arial, sans-serif;
  font-weight: 700;
  font-size: toRem(12);
  text-transform: uppercase;

  &__body {
    position: relative;
    display: flex;
    align-items: center;
  }

  &__text {
    margin: 0 auto;
	  color: $purple;
  }

  &:hover {
    .#{$button}__text {
     color: $dark-shade-50;
    }
  }

  &_filled, &_bordered {
    border-radius: 22px;
    height: toRem(44);
    width: toRem(99);
  }

  &_bordered {
    border: solid 2px transparent;
    background-image: linear-gradient(white, white), $purple-blue-gradient;
    background-origin: border-box;
    background-clip: content-box, border-box;
      
    &:hover {
      background-image: linear-gradient(white, white), $purple-blue-gradient-50;

      .#{$button}__text {
        color: $purple;
      }
    }
  }

  &_filled {
    background: $purple-blue-gradient;

    .#{$button}__text {
      color: white;
    }

    &:hover {
      opacity: 0.5;

      .#{$button}__text {
        color: white;
      }
    }
  }

  &_withIcon {
    min-width: toRem(120);
    max-width: toRem(320);
    width: 100%;

    .#{$button}__icon {
      position: absolute;
      width: toRem(24);
      height: toRem(24);
      right: toRem(10);
      color: white;
    }
  }
}